export default {
	data() {
		return {
			all:false,
			zong:[],
			scrollTop:0,
			number:0,
			show:true,
			guan:'管理',
			finish:'完成',
			ins:-1,
			active:'http://taotao111.test.upcdn.net/shopping_trolly_selected.png',
			path:'http://taotao111.test.upcdn.net/shopping_empty_xuanze.png',
			rentData: [
					{number:1, image: 'http://taotao111.test.upcdn.net/goods.png', title: '[洗涤]保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price: 36.00 },
					{number:1, image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price: 36.00},
					{number:1, image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price: 36.00},
					{number:1, image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price:36.00},
					{number:1, image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price:36.00},
					{number:1, image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price:36.00},
					{number:1, image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price:36.00},
					{number:1, image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price:36.0}
				
			]
		};
	},
	onShow() {
		let data = this.rentData
		for (let i = 0; i < this.rentData.length; i++) {
			this.rentData[i]['state'] = false
			this.rentData[i]['id'] = i
		}	
		console.log(this.rentData)
	},
	computed:{
		total(){
			let a = 0
				let zong = this.zong;
				for (let i = 0; i < zong.length; i++) {
					a += zong[i].price * zong[i].number
				}
				console.log(zong)
				return a
			},
			rentDatas(){
				return this.$store.state.car.car
			}
	},
	methods:{
		activeImg(value,key){
			if(value.state == false){
				this.rentData[key]['state'] = true
				this.zong.push(value);
			}else if(value.state == true){
				this.rentData[key]['state'] = false
				let zong = this.zong;
				let cid = value.id;
				for (let i = 0; i < zong.length; i++) {
					if(zong[i].id == cid){
						console.log(zong);
						zong.splice(i,1)
					}
				}
			}
			console.log(this.rentData.length,this.zong.length)
			if(this.rentData.length == this.zong.length){
				this.all = true
			}else{
				this.all = false
			}
		},
		plus(value) {
			if(value.state == false){
				value.number = Number(value.number)+1
				this.rentData.number = value.number;
			}else if(value.state == true){
				value.number = Number(value.number)+1
				this.rentData.number = value.number;
				let zong = this.zong;
				let cid = value.id;
				let _this = this;
				for (let i = 0; i < zong.length; i++) {
					if(zong[i].id == cid){
						Number(_this.zong[i].number)+1
					}
				}
			}
		},
		decre(value) {
			if(value.state == false){
				if(value.number <= 1){
					uni.showToast({
						icon:"none",
						title:"不能小于1"
					})
					return false
				} else if(value.number >= 1){
					value.number = Number(value.number)-1
					this.rentData.number = value.number;
				}
			}else if(value.state == true){
				
				if(value.number <= 1){
					uni.showToast({
						icon:"none",
						title:"不能小于1"
					})
					return false
				}
				value.number = Number(value.number)-1
				this.rentData.number = value.number;
				let zong = this.zong;
				let cid = value.id;
				let _this = this;
				for (let i = 0; i < zong.length; i++) {
					if(zong[i].id == cid){
						let num = Number(_this.zong[i].number)
						if(num <= 1){
							uni.showToast({
								icon:"none",
								title:"最少为一件哦"
							})
							return false
						}else if(num >= 1){
							Number(_this.zong[i].number)-1
						}
					}
				}
			}
		},
		allSelect(){
			let _this = this;
			let rentData = this.rentData;
			if(this.all == true){
				this.all = false
				for (let i = 0; i < rentData.length; i++) {
					_this.rentData[i]['state'] = false
				}
				this.zong = [];
			}else if(this.all == false){
				this.all = true;
				for (let i = 0; i < rentData.length; i++) {
					_this.rentData[i]['state'] = true
					this.zong.push(_this.rentData[i]);
				}
				
			}
		},
		change(){
			this.show = !this.show
		}
	}
};
